.flex {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  overflow: hidden;
  width: 100%;
}

.list {
  margin: 1% 0% 0% 5%;
}

.tetris, .dialog {
  padding-bottom: 1%;
}

.tetris {
  width: 200px;
}

.dialog {
  width: 50%;
}

@media only screen and (max-width: 600px) {
  .tetris, .dialog {
    width: 95%;
  }
}



